
import React,{ Component} from 'react';

import {connect} from 'react-redux'


import PieChart from './PieChart.js';
import BarChart from './BarChart.js';
import LinesChart from './LinesChart.js';
import {casesState} from '../Opendata/staticData.js';

//import LinePieChart from './LineChart.js';

class Graphic extends Component{

    constructor(props){
        super(props);

        this.state={ };
        
    }
    
    render(){
        let {User} = this.props;

        return(
            <div style={{height:'250px',paddingTop: '30px'}}>
                {
                    User.Filter === 'Localidad' ? <BarChart/>
                    :[
                        User.Filter === 'Sexo' ? <PieChart/> 
                        : [
                            User.Filter === 'Estado' ? <LinesChart  lineData={casesState} /> 
                            : null
                        ]
                    ] 
                }
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
       User: state.reducer_user
    };
};

export default connect(mapStateToProps,null)(Graphic);