import React from 'react';

import {Pie} from 'react-chartjs-2';
import {getDataLocalidades} from '../Opendata/data.js';
import Loader from './Loader'

export default class PieChart extends React.Component{


    constructor(props){
        super(props);
        this.state={
            dataIsLoaded: false,
            chartData:{
                title: 'Distribución de casos por genero',
                type: 'doughnut',
                labels: ['Masculino', 'Femenino'],
                datasets: [{
                  data: [220, 160],
                  backgroundColor: ['#47AEE9', '#F186B3'],
                  hoverBackgroundColor:['#47AEE6', '#F186B02'],
                }]
            }
        }
        //this.dataLoader = this.dataLoader.bind(this)
    }

    getData = async ()=>{
        var  datajson = await getDataLocalidades()
        datajson = datajson['genero']    
        let char_data = this.state.chartData
        char_data.datasets.map( dataset => { dataset.data = datajson; return null })
        this.setState({chartData: char_data})
        this.setState({dataIsLoaded: true})
    }

    componentDidMount(){
        console.log('dataIsLoaded',this.state.dataIsLoaded) 
        this.getData() 
    }

    
    render() {
        return (
        <div style={{height:'250px',paddingTop: '30px'}}>
            { !this.state.dataIsLoaded ? <Loader/> : null }
            <h2>{this.state.chartData.title}</h2>
            <Pie data={this.state.chartData} />
        </div>
        );
    }

};
