import React  from 'react';
import './loader.css'
 
const Loader = (props) => {
 
 
    return(
        <div style={{justifyContent: 'center'}} className="row">
            
            <div className="col-md-12">
            {
                props.type === "map" ? ''
                : <p>Estamos cargando los datos abiertos de Covid-19 para Bogotá</p>
            }
                
            </div>
            <div  style={{paddingLeft: '250px'}} className="col-md-10">
                <div  class="loader"></div>    
            </div>
        </div>
    )
 
}
export default Loader;