import {getPositions,getDataLocalidades} from '../../Components/Opendata/data'; 


export const change_data = (option) =>{
    return {
        type: 'CHANGE_DATA',
        payload: option
    }
}

export const get_positions = () =>{
    console.log('get_positions redux')
    return async (dispatch) => {
        let solve = await getPositions()
        dispatch(change_data(['PolyLoc',solve]))
        /*console.log("solve from redux: ",solve)
        setTimeout(() => {
            
        }, 1000);*/
    }
}

export const get_loc = () => {
    console.log('get_localidades redux')
    return async (dispatch) => {
        let solve = await getDataLocalidades()
        dispatch(change_data(['Loc',solve]))
        /*console.log("solve from redux: ",solve)
        setTimeout(() => {
            
        }, 1000);*/
    }
}